import {
  Card,
  CardHeader,
  CardTitle,
  CardContent,
  CardFooter
} from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Button } from "@/components/ui/button"
import {
  Select,
  SelectTrigger,
  SelectValue,
  SelectContent,
  SelectItem
} from "@/components/ui/select"
import { Switch } from "@/components/ui/switch"
import { Separator } from "@/components/ui/separator"

export const SettingsForm = () => {
  return (
    <Card className="">
      <CardHeader>
        <CardTitle>Site Settings</CardTitle>
      </CardHeader>

      <CardContent className="space-y-8">
        
        {/* GENERAL */}
        <div className="space-y-4">
          <h3 className="text-lg font-semibold">General Settings</h3>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <Label className="mb-4">Site Name</Label>
              <Input placeholder="My LMS" defaultValue="My LMS" />
            </div>
          </div>
        </div>

        <Separator />

        {/* BRANDING */}
        <div className="space-y-4">
          <h3 className="text-lg font-semibold">Branding</h3>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <Label className="mb-4">Site Logo</Label>
              <Input type="file" />
            </div>

            <div>
              <Label className="mb-4">Site Favicon</Label>
              <Input type="file" />
            </div>
          </div>
        </div>

        <Separator />

        {/* CONTACT INFO */}
        <div className="space-y-4">
          <h3 className="text-lg font-semibold">Contact Information</h3>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <Label className="mb-4">Site Email</Label>
              <Input type="email" placeholder="info@example.com" />
            </div>

            <div>
              <Label className="mb-4">Site Phone</Label>
              <Input placeholder="+123456789" />
            </div>

            <div className="md:col-span-2">
              <Label className="mb-4">Address</Label>
              <Input placeholder="Your office address" />
            </div>
          </div>
        </div>

        <Separator />

        {/* CURRENCY SETTINGS */}
        <div className="space-y-4">
          <h3 className="text-lg font-semibold">Currency Settings</h3>

          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <div>
              <Label className="mb-4">Base Currency</Label>
              <Input placeholder="USD" defaultValue="BDT" />
            </div>

            <div>
              <Label className="mb-4">Currency Symbol</Label>
              <Input placeholder="$" defaultValue="à§³" />
            </div>

            <div>
              <Label className="mb-4">Currency Position</Label>
              <Select defaultValue="right">
                <SelectTrigger>
                  <SelectValue placeholder="Position" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="left">Left</SelectItem>
                  <SelectItem value="right">Right</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>
        </div>

        <Separator />

        {/* CHARGES */}
        <div className="space-y-4">
          <h3 className="text-lg font-semibold">Charges</h3>

          <div>
            <Label className="mb-4">Site Charge (%)</Label>
            <Input type="number" step="0.01" defaultValue="15.00" />
          </div>

          <div className="flex items-center space-x-2 pt-2">
            <Switch id="has_space" />
            <Label htmlFor="has_space">Has Space Between Currency</Label>
          </div>
        </div>

        <Separator />

        {/* NOTIFICATIONS */}
        <div className="space-y-4">
          <h3 className="text-lg font-semibold">Notifications</h3>

          <div className="space-y-3">
            <div className="flex items-center justify-between">
              <Label>Email Notifications</Label>
              <Switch defaultChecked />
            </div>

            <div className="flex items-center justify-between">
              <Label>SMS Notifications</Label>
              <Switch defaultChecked />
            </div>

            <div className="flex items-center justify-between">
              <Label>In-App Notifications</Label>
              <Switch defaultChecked />
            </div>

            <div className="flex items-center justify-between">
              <Label>Firebase Notifications</Label>
              <Switch defaultChecked />
            </div>
          </div>
        </div>

      </CardContent>

      <CardFooter className="flex justify-end">
        <Button>Save Settings</Button>
      </CardFooter>
    </Card>
  )
}
